
@import "../../../style/variables";

.update-policy-selection-table-container {
  height: calc(100vh - 150px);
  margin-top: -10px;
  overflow: auto;

  .update-policy-selection-table {
    min-width: 550px;
    max-width: calc(100vw - 288px);
    margin: 0 auto;

    th {
      color: $pale-sky;
    }

    th, td {
      border-bottom: 1px solid $mystic;
      padding: 10px 0;
    }

    tr:last-child td {
      border-bottom: 0;
    }

    .dataframe-name {
      padding-left: 40px;
      padding-right: 20px;
      width: 60%;
      max-width: 300px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .policy-selection {
      padding-right: 40px;
      width: 40%;

      .ant-select {
        width: 100%;
        min-width: 110px;
      }
    }
  }
}

.ant-select-dropdown {
  z-index: 90;
}
