@import "../../style/variables";

.details-container {
  .ant-collapse {
    background: none;
    border: 0;
  }

  .ant-collapse {
    .ant-collapse-header {
      color: $dodger-blue;

      &::selection {
        background: transparent;
        color: inherit;
      }
    }

    .ant-collapse-content {
      color: $silver-chalice;
      border: 0;
    }
  }
}

.ant-modal {
  .details-container
  .ant-collapse {
    .ant-collapse-header {
      padding: 5px 20px;

      .ant-collapse-arrow {
        left: 0;
      }
  }

    .ant-collapse-item
    .ant-collapse-content-box {
      padding-left: 20px;
      font-size: 12px;
    }
  }
}
