@import "../../style/variables";

.dnd-drop-ready {
  background: rgba(2, 175, 255, 0.03);
}

.dnd-drop-targeted {
  outline: 2px $dodger-blue dashed;
  outline-offset: -2px;

  *[data-dnd-item] * {
    pointer-events: none;
  }
}

.dnd-dragged-item {
  opacity: 0.6;

  &:active,
  & *:active {
    cursor: move !important;
  }

  & * {
    pointer-events: none;
  }
}

.dnd-divider {
  width: 92%;
  height: 0px;
  outline: 1px dashed $dodger-blue;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  &.vertical {
    width: 0 !important;
    height: 90% !important;
    flex-direction: column !important;

    &.first {
      margin: 0 -10px 0 10px;
    }

    &.last {
      margin: 0 10px 0 -10px;
    }
  }

  // divider arrow drawing
  span.arrow {
    border: solid $dodger-blue;
    border-width: 0 1px 1px 0;
    display: inline-block;
    width: 8px;
    height: 8px;

    &.down {
      transform: rotate(45deg);
      margin: -10px -4px 0;
    }
    &.up {
      transform: rotate(-135deg);
      margin: 0 -4px -10px;
    }
    &.left {
      transform: rotate(135deg);
      margin: -4px -10px -4px 0;
    }
    &.right {
      transform: rotate(-45deg);
      margin: -4px 0 -4px -10px;
    }
  }
}

.drag-and-drop-ghost {
  position: absolute !important;
  top: -500px !important;
  left: -500px !important;
  background: rgba(28,145,220,0.10) url("../../style/icons_files/draggable_icon.svg") no-repeat right 6px center;
  border: 1px solid $dodger-blue;
  padding: 4px 28px 4px 8px;
  border-radius: 2px;

  // style handling for common situations to beautify items
  & * {
    opacity: 1;
    padding: 0 2px !important;
  }
  li { list-style: none; }
  div { display: inline-block; }
}

*[data-dnd-item][data-dnd-value] {
  &:hover {
    background: $alabaster url("../../style/icons_files/draggable_icon.svg") no-repeat right 6px center;
    padding-right: 18px;
    transition: padding 0.2s ease-out;
  }
}
