@import "../../../style/variables";

.dataframes-selection-panel {
  .dataframes-selection-container {
    height: calc(100vh - 180px);
    padding: 10px 12px 0 16px;

    overflow: auto;

    h2 {
      color: $pale-sky;
      font-size: 12px;
      font-weight: bold;
    }

    .dataframes-selection-list {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin: 4px 0;
        padding: 4px 0 4px 10px;
        display: flex;
        align-items: center;
        cursor: pointer;

        &.previewed {
          background: $white;
        }

        .ant-checkbox-wrapper {
          margin-right: 10px;
        }

        div {
          display: inline-block;
          max-width: 148px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
