$focus-outline: #1890ff;

.header {
  .logo {
    .mstr-logo-container {
      height: 30px;
    }
  }

  .left-side {
    display: flex;
  }

  .top-nav {
    height: 58px;
    color: #333333;
    font-weight: 500;
    font-size: 18px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: 34px;
    padding: 4px 20px 0 30px;

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: inline-block;

            &:first-child {
                margin-right: 30px;
            }

            button {
                font-size: 14px;
                color: #888;
                background: none;
                padding: 10px 5px;
                margin-right: 10px;
                cursor: pointer;
                border: none;
                line-height: 14px;

                &.active {
                    color: #555;
                    border-bottom: 3px solid #1C91DC;
                }

                &:hover {
                    border-bottom: 3px solid rgb(211, 235, 250);
                }

                &:focus {
                    outline: 3px solid $focus-outline;
                }
            }
          }
      }
    }
}
