@import "../../../style/variables";

$breakpoint1: 450px;
$breakpoint2: 500px;
$focus-outline: #1890ff5c;

.tree-view-folder-content {
    overflow: auto;

    .section {
        h3 {
            clear: both;
            font-size: 10px;
            color: #627282;
            border-top: 1px solid $athens-gray;
            border-bottom: 1px solid $athens-gray;
            padding: 3px;
        }

        &:first-child {
            h3 {
                border-top: none;
            }
        }

        .folder-item {
            background: none;
            border: 0;
            width: 120px;
            height: 130px;
            overflow: hidden;
            font-size: 14px;
            padding: 5px;
            float: left;
            text-align: center;
            margin: 5px;

            &:hover {
                background: none;
            }

            @media (max-width: $breakpoint1) {
                width: 80px;
                height: 95px;
                font-size: 12px;
            }

            .icon {
                width: 100px;
                height: 80px;
                text-align: center;
                margin: 0 auto;

                @media (max-width: $breakpoint1) {
                    width: 70px;
                    height: 50px;
                }

                svg {
                    width: 100%;
                    height: 100%;
                    max-width: none;
                    max-height: none;
                }
            }

            .name {
                overflow: hidden;
                text-overflow: ellipsis;
                line-height: 120%;
                padding-top: 5px;
                padding-bottom: 1px;

                &::selection {
                    background: transparent;
                    color: inherit;
                }
            }

            &.selectable,
            &.openable {
                cursor: pointer;

                &:hover,
                &:focus {
                    outline: 1px solid $focus-outline;
                    background: #f6f6f6;
                }

                &.selected {
                    background: #E6EDF6;
                    border: 1px solid #C7DDF3;
                    padding: 4px;
                }
            }

        }
    }
}

.import.browser-content {
    display: flex;
    .tree-view-folder-content {
        max-height: calc(100vh - 150px);
    }
}

.content .directory-selection-content {
    display: flex;

    .tree-view-folder-content {
        max-height: calc(100vh - 284px);

        @media(max-width: $breakpoint2) {
            height:calc(100vh - 256px);
        }
    }
}