@import "../../../style/variables";

.details-box {
  min-width: 260px;
  height: calc(100% - 40px);
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 20px;
  border: 1px solid $alto;

  .ant-checkbox-wrapper {
    margin: 0 9px 0 3px;
  }

  svg {
    height: 14px !important;
    min-height: 14px !important;
    width: 14px !important;
    min-width: 14px !important;
    margin-right: 8px;
  }

  .red-info svg {
    margin: 0;
  }

  .categorybox-holder {
    flex-basis: 100%;
    align-self: flex-start;
    height: 100%;
    width: 100%;
    overflow: hidden;

    .selector-title {
      font-weight: normal !important;
      white-space: nowrap;
      padding: 12px 9px 6px;
      margin: 0 !important;
    }

    .categorybox-content {
      padding: 0 6px;
      height: calc(100% - 38px);
      overflow-y: auto;
      position: relative;
    }

    &:nth-child(2) .categorybox-content {
      border-bottom: 1px solid $alto;
    }

    .main-list {
      list-style: none;
      margin: 0;
      padding: 0;
      white-space: nowrap;
      height: 100%;

      .ant-empty-normal {
        margin: 0;
        padding: 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }

    &.loading-holder {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;

      img {
        height: 25px;
      }
    }

    li {
      line-height: 14px;
      padding: 6px 3px;
      display: flex;
      align-content: center;
      justify-content: space-between;

      .text-to-input-button {
        max-width: 170px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .mstr-icon {
        justify-self: right;
      }
    }

    span {
      padding: 0;
      font-size: 12px;
    }
  }

  &.previewed {
    outline: 2px solid $dodger-blue;
    outline-offset: -2px;
  }
}

.box-title-container {
  white-space: nowrap;
  padding: 6px 9px;
  border-bottom: 1px solid $alto;
  margin: 0 0 0 0 !important;
  height: 100%;
  max-height: 30px;
  line-height: 18px;
  padding: 6px 9px;

  span {
    padding: 0 !important;
    font-size: 13px;

    button {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 220px;
      text-align: left;
    }
  }
}

.arrow-holder {
  background: #fff;
  z-index: 1;
  border: 1px solid $alto;
  border-radius: 4px;
  height: 18px;
  width: 42px;
  position: relative;
  margin: -9px 0 -10px;
  align-self: center;

  button {
    margin: 0;
    min-width: 20px !important;
    height: 16px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
    padding: 0;
    line-height: 0;
    transform: rotate(90deg);
  }

  button,
  button:focus,
  button.focus-visible,
  button:hover {
    box-shadow: none;
    transition: none;
    background: transparent;
    border: 0;
  }

  span {
    transform: translateY(-1px);
  }
}
