    z-index: 14;
    background: #fff;
    white-space: nowrap;
    display: flex;
}

.main-content:not(.authentication) {
    grid-row-start: 2;
    grid-row-end: 3;
    overflow: auto;
}

#root .footer {
    grid-row-start: 3;
    grid-row-end: 4;
    border-top: 1px solid #e7e7e7;
    z-index: 13;
    background: #fff;
}

#root .footer-content {
    display: flex;
    justify-content: space-between;
}

