@import "../../../style/variables";

.directory-selection {
  .left-panel {
    width: 324px;
    border: none;

    .envir-name-container {
      padding: 10px 0 5px 10px;

      .envir-name {
        padding-left: 5px;
        color: $emperor;
        font-size: 12px;
      }
    }

    .folder-tree-container {
      min-height: calc(100vh - 184px);
      max-height: calc(100vh - 184px);
      height: calc(100vh - 184px);

      .ant-spin-container {
        overflow: auto;
      }
    }

    &:focus {
      outline: none;
    }

    &.focus-visible {
      background: rgba(24, 144, 255, 0.1) !important;
      outline: none;
    }

    .folder-tree {
      background: none;
      border: none;
      overflow: visible;
      min-height: calc(100vh - 189px);
      float: left;
      min-width: 100%;
      padding: 0 3px 0 5px;

      .ant-tree-title {
        width: unset;
      }
    }

    .loading-indicator-tree {
      height: 100%;
    }

    .folder-tree li span.ant-tree-node-content-wrapper {
      padding: 0;
      margin-left: -5px;
      min-height: 24px;
      height: inherit;
    }

    .folder-tree .ant-tree-child-tree > li:first-child {
      padding-top: 0;
    }
  }

  .right-panel {
    .name-desc-form {
      display: table;
      border-spacing: 15px;
      border-bottom: 1px solid $athens-gray;

      .form-row {
        display: table-row;
      }

      .label-container,
      .field-container {
        display: table-cell;
        vertical-align: top;
      }

      label {
        font-size: 13px;
        color: $emperor;
        line-height: 13px;
        width: 0.1%;
        white-space: nowrap;
      }

      .label-container {
        padding-top: 7px;
      }

      .field-container {
        width: 100%;
      }

      .field {
        width: 100%;
        padding: 6px 0 5px 11px;
        border: 1px solid $mercury;
        font-size: 12px;
        color: $emperor;

        &:focus:not(.invalid) {
          outline: 2px solid $dodger-blue;
        }
      }

      textarea {
        resize: none;
        height: 50px !important;
      }

      input {
        &.invalid {
          outline: none;
          border: 1px solid crimson;
        }
      }

      .textarea-length {
        color: $nobel;
        font-size: 11px;
        text-align: right;
        margin-bottom: -15px;
        padding-right: 2px;
      }
    }
  }
}

#new-folder-name-input {
  width: 400px;
  border: 1px solid $mercury;
  padding: 5px;

  &:focus {
      outline: none;
  }

  &.invalid {
      border: 1px solid crimson;
  }
}

.footer-right {
  display: flex;
  align-items: center;

  .certify-container {
    margin-right: 10px;

    label {
      cursor: pointer;
      color: $emperor;

      &::selection {
        background: transparent;
        color: inherit;
      }
    }

    .mstr-icon,
    .ant-checkbox-wrapper {
      position: relative;
      top: 2px;
    }

    .mstr-icon {
      padding: 0 8px;
      svg {
        cursor: pointer;
        margin-left: 0;
      }
    }
  }
}
