/* Container */
.box-container {
    width: 1032px;
    margin-top: 40px;
    background-color: #ffffff;
    margin: 0 auto;
}

/* Header and Footer */
.header,
.footer {
    padding: 28px 24px;
    width: 100%;
}

.header {
    border-bottom: 1px solid #979797;
    height: 85px;
}

.footer {
    height: 89px;
}

.header .title {
    font-size: 18px;
    vertical-align: top;
    padding: 0;
    color: #444649;
    display: inline-block;
}

.header .mstr-logo {
    height: 28px;
    float: left;
}

.header .connector-logo {
    height: 28px;
    float: right;
}

.footer {
    border-top: 1px solid #979797;
}

.footer button {
    display: inline-block;
}

/* Main content */
.main-content {
    padding: 18px 64px 30px 44px;
}

.section-box {
    border: 1px solid rgba(151, 151, 151, 0.55);
    padding: 20px;
    /* Only for mstr dataset */
    padding-top: 10px;
    padding-bottom: 14px;
    font-size: 13px;
}

.ant-collapse-item {
    border: 0;
}

.ant-collapse-borderless>.ant-collapse-item>.ant-collapse-content>.ant-collapse-content-box {
    padding: 0;
    padding-left: 40px;
}

.ant-collapse>.ant-collapse-item>.ant-collapse-header .arrow {
    color: #979797;
    font-size: 18px;
    left: 20px;
}

.ant-collapse>.ant-collapse-item>.ant-collapse-header .arrow:before {
    content: "\25b8";
}

.ant-collapse>.ant-collapse-item {
    border: 0;
}

/* Form */
.ant-input {
    font-size: 13px;
}

.ant-form-item-label {
    text-align: left;
}

.ant-form-item {
    margin-bottom: 8px;
}

/* Layout helper elements */
.v-divider {
    display: inline-block;
    border-left: 1px solid rgba(151, 151, 151, 0.55);
    margin: 0 10px;
    height: 28px;
}

.h-divider {
    border-bottom: 1px solid rgba(151, 151, 151, 0.55);
    margin-bottom: 4px;
    height: 1em;
    width: 100%;
}

.ml-auto {
    float: right;
}

.border-right {
    border-right: 1px solid rgba(151, 151, 151, 0.55);
}

/* Selector buttons */
.ant-btn {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 4px;
    color: #444649;
}

.ant-btn:hover {
    background: #F3F3F3;
    border: 1px solid #D2D2D2;
    border-radius: 4px;
    color: #444649;
}

.ant-btn:focus {
    border: 1px solid #D2D2D2;
    box-shadow: #1C91DC 0 0 8px;
    border-radius: 4px;
    color: #444649;
}

.ant-btn.ant-btn-primary {
    color: #ffffff;
    background: #1C91DC;
    border: 1px solid #1C91DC;
    border-radius: 4px;
}

.ant-btn.ant-btn-primary:hover {
    background: #1384CC;
    border: 1px solid #1384CC;
    border-radius: 4px;
    color: #ffffff;
}

.ant-btn.ant-btn-primary[disabled] {
    background: #f5f5f5;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.25);
}

.selector-button.ant-btn {
    border: none;
    margin: 0;
    padding-right: 0;
    padding-left: 10px;
    height: auto;
    font-size: 10px;
    line-height: 1;
    color: #1890ff;
}

.preview-button {
    border: none;
    margin-top: 10px;
    height: auto;
    color: #1890ff;
    z-index: 1;
}

.preview-button .anticon,
.preview-button .anticon:hover {
    color: #979797;
}

/* Preview modal */
.ant-modal-mask {
    background-color: rgba(255, 255, 255, 0.7);
}

.ant-modal-content {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(151, 151, 151, 0.55);
}

.ant-modal-header {
    background-color: #ededed;
    padding: 8px 12px;
}

.ant-modal-title {
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
}

.ant-modal-close-x {
    width: 36px;
    height: 36px;
    line-height: 36px;
    font-weight: 400;
}

.ant-modal-body .ant-table-body {
    font-size: 12px;
    color: #444649;
    line-height: 30px;
}

/* Table */
.ant-table {
    font-size: 11px;
    font-weight: bolder;
}

.ant-table td {
    white-space: nowrap;
}

.ant-table-body {
    overflow-x: auto !important;
}

.preview-note {
    padding: 5px 0;
    font-size: 11px;
    text-align: right;
}

/* Icons */
.mstr-icon svg {
    max-width: 18px;
    max-height: 18px;
    vertical-align: text-top;
}

.mstr-icon.selector svg {
    max-height: 14px;
    vertical-align: sub;
    margin-right: 5px;
}

.filter-list .mstr-icon.selector svg {
    max-height: 20px;
    vertical-align: middle;
    margin-left: 5px;
}

.filter-list .ant-tree li span.ant-tree-iconEle {
    vertical-align: sub;
}

.mstr-icon.certified svg {
    margin-left: 15px;
}

.mstr-icon.no-data svg {
    max-width: 64px;
    max-height: 41px;
}

.mstr-icon.hidden {
    margin-left: 5px;
}

.ant-popover .ant-popover-inner-content {
    padding: 8px;
    background-color: #404040;
    color: white;
    border-radius: 4px;
    font-size: 12px;
    overflow-wrap: break-word;
}

.ant-popover.ant-popover-placement-bottom>.ant-popover-content>.ant-popover-arrow,
.ant-popover.ant-popover-placement-bottomRight>.ant-popover-content>.ant-popover-arrow,
.ant-popover.ant-popover-placement-bottomLeft>.ant-popover-content>.ant-popover-arrow {
    border-color: #404040;
}
