@import "../../style/variables";

$initials-circle-size: 30px;
$user-tooltip-hide-breakpoint: 270px;

.ant-popover.user-tooltip-overlay {
    width: 230px;
    position: relative;

    @media (max-width: $user-tooltip-hide-breakpoint) {
        display: none;
    }

    .ant-popover-content {
        position: relative;
        top: 7px;
        left: 25px;
    }

    .ant-popover-inner-content {
        background: #fff;
        color: $abbey;
        padding: 0;
        text-align: left;
    }

    .user-info {
        display: flex;
        padding: 10px;
        border-bottom: 1px solid $mystic;

        .circle {
            color: #fff;
            background: $nobel;
            width: $initials-circle-size;
            min-width: $initials-circle-size;
            height: $initials-circle-size;
            border-radius: 50%;
            text-align: center;
            font-family: sans-serif;
            position: relative;
            top: 5px;

            .initials {
                font-size: calc(#{$initials-circle-size} / 2);
                font-weight: 300;
                line-height: 1;
                position: relative;
                top: calc(#{$initials-circle-size} / 4);
            }
        }

        .name-url {
            padding-left: 10px;

            h3 {
                font-size: 12px;
                margin: 0;
            }

            p {
                color: $abbey;
                font-size: 10px;
                width: 150px;
                margin: 0;
                word-break: break-all;
            }
        }
    }

    .links {
        ul {
            list-style: none;
            padding: 0;
            margin: 0;
        }

        li {
            a, button {
                color: $abbey;
                font-size: 12px;
                display: block;
                padding: 8px 15px;
                background: transparent;
                border: none;
                width: 100%;
                text-align: left;
                cursor: pointer;

                &:hover {
                    background: $alabaster;
                }

                &:focus {
                    background: $alabaster;
                    outline: 3px solid $dodger-blue;
                    outline-offset: 3px;
                    box-shadow: none;
                }

                &[disabled] {
                    cursor: initial;
                    opacity: 0.5;

                    &:hover {
                        background: transparent;
                    }
                }
            }
        }

        .version-number {
            font-weight: 300;
            text-align: center;
            color: $dodger-blue;
            padding: 10px;
        }
    }
}

.user-tooltip {
    font-size: 28px;
    font-weight: 900;
    margin-top: -8px;
    max-height: 44px;
    width: 13px;

    @media (max-width: $user-tooltip-hide-breakpoint) {
        display: none;
    }

    a {
        display: block;
        padding: 1px 0px;
        color: $abbey;
    }

    button {
        all: unset;
        cursor: pointer;

        &:focus {
            outline-offset: 2px;
        }
    }
}
