@import "../../style/variables";

.ant-modal {
  .ant-modal-confirm-title {
    font-size: 14px;
    color: $abbey;
  }

  .ant-modal-confirm-content {
    p {
      font-size: 12px;
      margin-bottom: 1px;
    }
  }
}

.loading-modal {
  p {
    margin: 0;
  }

  .cancel-info {
    font-size: 12px;
    line-height: 25px;
    color: $silver-chalice;

    .stop-icon {
      font-size: 0;
      background: url('data:image/png;base64,');
      background-size: 25px 24px;
      display: inline-block;
      width: 21px;
      height: 20px;
      position: relative;
      top: -3px;
      left: -2px;
    }
  }
}
