$user-tooltip-hide-breakpoint: 270px;
$focus-outline: #1890ff;

.search-bar {
    box-shadow: 0 0 10px 1px #ccc;
    margin-bottom: 10px;
    position: relative;
    z-index: 13;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    padding: 0 10px;

    h2 {
        font-size: 14px;
        margin: 0;
    }

    .object-search {
        height: 26px;

        input {
            width: 220px;
            float: right;
        }
    }
}
