@import "../../style/variables";

.mstr-popover {
  .ant-popover-content {
    .ant-popover-arrow:not(.foo) {
      display: none;
    }

    .ant-popover-inner {
      box-shadow: none;
      border: 1px solid $mystic;
      border-radius: 0;

      .ant-popover-inner-content {
        background: #fff;
        color: black;
        text-align: left;

        h3 {
          color: $pale-sky;
          margin-bottom: 3px;
          font-size: 14px;
        }

        p {
          font-size: 12px;
          margin: 0;
          white-space: nowrap;
        }
      }
    }
  }
}
