.ant-row.full-height {
    height: 100%;
}

.ant-table-fixed-header .ant-table-scroll .ant-table-header {
    overflow-y: hidden;
}

.import-header {
    display: flex;
}

.ant-table-placeholder {
    color: #515356;
    border-bottom: none;
}

.ant-table-column-sorters {
    display: flex !important;
    align-items: center;
    justify-content: space-between;
}

.ant-table-column-sorter .off {
    opacity: 0.2;
    display: block;
}

.ant-table-column-sorter .on {
    display: block;
}

.ant-table-column-title {
    display: ruby-base !important;
}

@media (max-width: 576px) {
    .ant-row.full-height {
        height: auto;
    }

    .ant-row>.full-height {
        height: auto;
    }
}

.browser-content {
    margin-top: -10px;
}

#root .content .left-panel {
    background: #EEF1F3;
    height: calc(100vh - 150px);
    overflow: auto;
    border-right : 1px solid #e7e7e7;
    width: 240px;
    overflow-x: hidden;
    overflow-y: auto;
    float: left;
}

.content .left-panel .dataset-properties {
    height: calc(100vh - 180px);
    overflow: auto;
    padding-left: 10px;
    background: none;
    width: 239px;
}

.content .left-panel .dataset-properties .ant-collapse-content .ant-collapse-content-box {
    padding-left: 0;
}

.content .left-panel .dataset-properties input[type="text"] {
    margin: 6px;
}

/* Collapse panel header */
.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header {
    padding: 0;
    margin: 5px;
    font-size: 12px;
    color: #627282;
}

.left-panel .ant-collapse > .ant-collapse-item:first-child > .ant-collapse-header {
    margin-top: 10px;
}
.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header:focus {
    outline: 3px solid #1890ff;
    outline-offset: 1px;
    box-shadow: none;
    transition: none;
}

.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
    left: 200px;
}

.left-panel table {
    font-size: 14px;
    color: #515356;
}

.left-panel label {
    cursor: pointer;
    -ms-user-select: none;
    user-select: none;
}

.left-panel table td {
    padding: 0 0 5px 5px;
}

.left-panel h4:not(:nth-child(1)) {
    margin-top: 10px;
}

.left-panel table tr td:nth-child(2) {
    width: 145px;
    max-width: 145px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-left: 5px;
    vertical-align: middle;
    height: 14px;
}

.left-panel table tr td:nth-child(3) {
    width: 50px;
    text-align: right;
}

.left-panel .ant-input-search {
    width: 205px;
    margin-bottom: 7px;
}

#manage-applications {
    display: block;
    width: 140px;
    text-align: center;
    margin: 5px auto;
    border: 1px solid #627282;
    color: #627282;
    padding: 1px;
    border-radius: 2px;
    font-size: 12px;
}

.browser-content .right-panel {
    float: left;
    width: calc(100vw - 240px);
}

.clear-all-button {
    margin-top: 5px;
    border: 0;
    background: none;
    cursor: pointer;
    color: #3492ED;
    float: right;
    padding-right: 10px;
}
