
@import url('https://fonts.googleapis.com/css?family=Roboto:300,350,400,700&display=swap');

html {
    --antd-wave-shadow-color: transparent;
}

body {
    margin: 0;
    background-color: #fff;
    font-family: 'Roboto', Arial, Verdana, Helvetica, sans-serif;
    font-size: 13px;
    color: #444649;
}

/* Container */
#root > .content {
    width: 100%;
    height: 100vh;
    margin: 0;
    display: grid;
    grid-template-rows: 50px auto 50px;
}

/* Header and Footer */
#root .header,
#root .footer {
    height: 50px;
    padding: 8px 10px 0 10px;
    width: 100vw;
}

#root .header {
    grid-row-start: 1;
    grid-row-end: 2;
    border-bottom: 1px solid #e5e5e5;
    z-index: 14;
    background: #fff;
    white-space: nowrap;
    display: flex;
}

.main-content:not(.authentication) {
    grid-row-start: 2;
    grid-row-end: 3;
    overflow: auto;
}

#root .footer {
    grid-row-start: 3;
    grid-row-end: 4;
    border-top: 1px solid #e7e7e7;
    z-index: 13;
    background: #fff;
}

#root .footer-content {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 450px) {
    #root .footer {
        white-space: nowrap;
        overflow: auto;
    }
}

.header {
    display: flex;
    justify-content: space-between;
}

.header .title {
    line-height: 28px;
    font-size: 18px;
    vertical-align: top;
    padding: 0;
    color: #444649;
    overflow: hidden;
    text-overflow: ellipsis;
}

.header .mstr-logo {
    height: 28px;
    float: left;
    margin-right: 5px;
    float: left;
}

.header .mstr-logo svg {
    max-width: unset;
    max-height: unset;
}

.header .connector-logo {
    height: 28px;
    float: right;
}

.footer span {
    display: inline-block;
    cursor: initial !important;
}

.footer button {
    display: inline-block;
    cursor: pointer !important;
}

/* Main content */
.main-content {
    padding: 0 10px 0 10px !important;
}

.section-box {
    border: 1px solid rgba(151, 151, 151, 0.55);
    padding: 20px;
    /* Only for mstr dataset */
    padding-top: 10px;
    padding-bottom: 14px;
    font-size: 13px;
}

.ant-collapse-item {
    border: 0;
}

.ant-collapse-borderless > .ant-collapse-item > .ant-collapse-content > .ant-collapse-content-box {
    padding: 0;
    padding-left: 40px;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header .arrow {
    color: #979797;
    font-size: 18px;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header .arrow:before {
    content: "\25b8";
}

.ant-collapse > .ant-collapse-item {
    border: 0;
}

/* Form */
.ant-input {
    font-size: 13px;
}

.ant-form-item-label {
    text-align: left;
}

.ant-form-item {
    margin-bottom: 8px;
}

/* Layout helper elements */
.v-divider {
    display: inline-block;
    border-left: 1px solid rgba(151, 151, 151, 0.55);
    margin: 0 10px;
    height: 28px;
}

.h-divider {
    border-bottom: 1px solid rgba(151, 151, 151, 0.55);
    margin-bottom: 4px;
    height: 1em;
    width: 100%;
}

.ml-auto {
    float: right;
}

.border-right {
    border-right: 1px solid rgba(151, 151, 151, 0.55);
}

/* Selector buttons */
.selector-button {
    border: none;
    margin: 0;
    padding-right: 0;
    padding-left: 10px;
    height: auto;
    font-size: 10px;
    line-height: 1;
    color: #1890ff;
}

.preview-button {
    border: none;
    margin-top: 10px;
    height: auto;
    color: #1890ff;
}

.preview-button .anticon,
.preview-button .anticon:hover {
    color: #979797;
}

/* Preview modal */
.ant-modal-mask {
    background-color: rgba(255, 255, 255, 0.7);
}

.ant-modal-content {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(151, 151, 151, 0.55);
}

.ant-modal-header {
    background-color: #ededed;
    padding: 8px 12px;
}

.ant-modal-title {
    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
}

.ant-modal-close-x {
    width: 36px;
    height: 36px;
    line-height: 36px;
}

/* Table */
.ant-table {
    font-size: 11px;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
}

.ant-table td {
    white-space: nowrap;
}

.ant-table-body {
    overflow-x: auto !important;
}

.modal-preview .ant-table-body {
    overflow-x: visible !important;
}

.modal-preview .ant-table-body .attribute-column {
    text-align: left;
}

.modal-preview .ant-table-content > .ant-table-scroll > .ant-table-body {
    margin: 0;
}

.modal-preview .ant-table-body .metric-column {
    text-align: right;
}

#mstr-table.ant-table-small > .ant-table-content > .ant-table-body > table {
    padding: 0;
}

#mstr-table .ant-table-thead > tr > th {
    background-color: #ededed;
    overflow: hidden;
    white-space: nowrap;
}

.preview-note {
    padding: 5px 0;
    font-size: 11px;
    text-align: right;
}

/* Icons */
.mstr-icon svg {
    max-width: 18px;
    max-height: 18px;
    vertical-align: text-top;
}

.mstr-icon.selector svg {
    max-height: 14px;
    vertical-align: text-bottom;
    margin-right: 5px;
}

.icon-column .mstr-icon.dataset svg {
    max-width: 22px;
    vertical-align: text-bottom;
}

.mstr-icon.certified svg {
    margin-left: 0;
    vertical-align: text-bottom;
}

.authentication .mstr-logo svg {
    width: 70px;
    height: 70px;
    max-width: unset;
    max-height: unset;
}

/* Tooltip */
.info-tooltip {
    z-index: 13;
}

.info-tooltip.one-liner {
    max-width: unset;
}

.ant-tooltip-inner {
    font-size: 12px;
    min-height: unset;
    padding: 3px 5px;
    background: #000;
}

.ant-tooltip-placement-top .ant-tooltip-arrow,
.ant-tooltip-placement-topLeft .ant-tooltip-arrow,
.ant-tooltip-placement-topRight .ant-tooltip-arrow {
    border-top-color: #000;
}

.ant-tooltip-placement-bottom .ant-tooltip-arrow,
.ant-tooltip-placement-bottomLeft .ant-tooltip-arrow,
.ant-tooltip-placement-bottomRight .ant-tooltip-arrow {
    border-bottom-color: #000;
}

.ant-tooltip-placement-right .ant-tooltip-arrow,
.ant-tooltip-placement-rightTop .ant-tooltip-arrow,
.ant-tooltip-placement-rightBottom .ant-tooltip-arrow {
    border-right-color: #000;
}

.ant-tooltip-placement-left .ant-tooltip-arrow,
.ant-tooltip-placement-leftTop .ant-tooltip-arrow,
.ant-tooltip-placement-leftBottom .ant-tooltip-arrow {
    border-left-color: #000;
}

.ant-tooltip.no-arrow .ant-tooltip-arrow {
    display: none;
}

/* Version tag */
#version {
    display: inline-block;
    font-size: 12px;
    padding-left: 10px;
    vertical-align: text-bottom;
    height: 25px;
    color: rgba(0, 0, 0, 0.25);
}

/* Tooltip */
.rc-tooltip-inner {
    padding: 20px 20px;
    color: #444649;
    text-align: left;
    text-decoration: none;
    background-color: #ffffff;
    border-radius: 6px;
    -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    min-height: 34px;
  border: solid #bebfc0 1px;
    border-radius: 4px;
    box-shadow: 0 0 8px 0 #827d7d;
}

.rc-tooltip-placement-topLeft .rc-tooltip-arrow {
    left: 35px;
    bottom: 10px;
    margin-left: -5px;
    border-width: 0 0 0;
    border-top-color: transparent;
}

.rc-tooltip-arrow:after,
.rc-tooltip-arrow:before {
	top: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.rc-tooltip-arrow:after {
	border-color: rgba(255, 255, 255, 0);
	border-top-color: #ffffff;
	border-width: 6px;
	margin-left: -6px;
}
.rc-tooltip-arrow:before {
	border-color: rgba(82, 82, 82, 0);
  border-top-color: #bebfc0;
	border-width: 9px;
    margin-left: -9px;
}

.tooltip-header {
    display: inline-block;
    text-align: right;
  color: #a2a2a4;
    padding-right: 15px;
}

.tooltip-description {
    display: inline-block;
}

.rc-tooltip {
    opacity: 1;
}

.tooltip-first-line {
    margin-bottom: 7px;
}

/* FolderBrowser */
.content .table-container.object-browser {
    margin: 0;
    overflow: hidden;
}

.content .ant-table-fixed-header .ant-table-scroll .ant-table-header {
    overflow: auto;
    z-index: 12;
    margin-bottom: 0;
    padding-bottom: 0;
    position: relative;
    padding: 0 15px;
    background: #fff;
    border-bottom: 1px solid #e8e8e8
}

.ant-table-header .ant-table-fixed {
    min-width: calc(100% - 5px);
}

.content[mode="browsing"] .ant-table-thead > tr > th {
    border-bottom: none;
}

#connectButton.ml-auto {
  float: left;
}

/* remove doubled search icon */
#search-toolbar > div > span > span > i.anticon-search:before {
    content: none;
}

.content .ant-row.full-height,
.context .ant-row .full-height {
  height: calc(100vh - 159px);
  width: 100vw;
}

.footer button {
    margin-left: 8px;
}

.footer > button {
    margin: 0;
}

.content .smart-folders-empty-table .ant-table-placeholder {
    border: 0;
}

.ant-modal-content .ant-table-wrapper {
    max-height: calc(100vh - 300px);
    overflow: auto;
}

.ant-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.zoom-leave,
.ant-modal.zoom-enter, .ant-modal.zoom-appear,
.ant-modal-zoom-leave, .ant-modal-zoom-disappear {
    animation-duration: 0s;
}

.fade-leave,
.fade-enter.fade-enter-active, .fade-appear.fade-appear-active {
    animation: none;
    opacity: 1;
    animation-duration: 0s;
}

.ant-modal-close-icon:before {
    content: none;
}

.ant-btn {
    min-width: 90px;
}

.flex-container {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

div .ant-tooltip.info-tooltip .ant-tooltip-inner {
    text-align: center;
}

div .ant-tooltip.hidden {
    display: none;
}

.center-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}
/* Remove animations/glows from buttons */
.content .ant-btn:focus {
    outline: 3px solid #1890ff;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

.content .ant-btn:after {
    display: none;
}

.ant-checkbox {
    animation: none;
}

.ant-tree-checkbox-checked:after,
.ant-checkbox-checked:after,
.ant-radio-checked:after {
    animation: none;
    border: none;
    transition: none;
}

.ant-checkbox-inner,
.ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-tree-checkbox-inner:after,
.ant-tree-checkbox-checked .ant-tree-checkbox-inner:after,
.ant-checkbox-checked .ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-radio-checked .ant-radio-inner:after,
.ant-radio-inner,
.ant-radio-inner:after {
    transition: none;
}

.ant-radio-input:focus + .ant-radio-inner {
    box-shadow: none;
}

input[tabfocused="1"] + .ant-radio-inner,
input[tabfocused="1"] + .ant-checkbox-inner {
    outline: 3px solid #1890ff;
    outline-offset: 1px;
    box-shadow: none;
}

/* Search input */
.content .ant-input-affix-wrapper .ant-input:not(:last-child) {
    padding-left: 30px;
    padding-right: 7px;
}

button:focus {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

button:focus:not(.focus-visible) {
    outline: none !important;
    box-shadow: none !important;
    transition: none !important;
}

[tabfocused="1"] {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

.ant-btn:focus:not(.some) {
    box-shadow: none;
}

.whole-page-loading {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

input[type="checkbox"].focus-visible {
    box-shadow: none !important;
}

.ant-btn.ant-btn-primary[disabled][type="button"] {
    color: #ffffff;
    background: #1C91DC;
    border: 1px solid #1C91DC;
    border-radius: 4px;
    opacity: 0.5;
}

.ant-btn[disabled] {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 4px;
    color: #444649;
    opacity: 0.5;
}

.selected-element {
    display: inline-block;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 180px;
    line-height: 24px;
    height: 24px;
}

.ant-radio-button-wrapper:hover {
    color: initial;
    background: #F3F3F3;
}

/* .ant-input-affix-wrapper {
    float: none;
    max-width: none;
    min-width: 0;
    width: auto;
} */

.content .ant-input-affix-wrapper .ant-input-suffix {
    left: 12px;
    right: 0;
    width: 16px;
}
