@import "../../../style/variables";

.data-modeling-container {
  .ant-col.right-panel {
    height: calc(100vh - 150px);
    display: grid;
    grid-template-rows: 67% 33%;

    .display-boxes-container {
      border-bottom: 1px solid $mystic;
      display: flex;
      align-items: center;
      overflow-x: auto;

      & > *:not(.lets-get-started-placeholder):last-child:after {
        content: "";
        display: block;
        position: absolute;
        right: -20px;
        width: 20px;
        height: 1px;
      }
    }
  }

  .lets-get-started-placeholder {
    width: 100%;
    text-align: center;

    h2 {
      font-size: 18px;
      margin: 20px 0;
    }

    p {
      font-size: 12px;
      width: 300px;
      margin: 0 auto;
    }

    svg {
      max-width: unset;
      max-height: unset;
      width: 189px;
    }
  }
}
