.preview-table-container {
  height: 100%;
  width: 100%;
  overflow: auto;

  .description-holder {
    margin: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
  }

  .preview-note span:first-child {
    margin-right: 2px;
  }

  .table-holder {
    height: calc(100% - 40px);
    margin: 0 auto;
    overflow: auto;
    width: calc(100vw - 280px);
    vertical-align: top;

    th {
      padding: 7px 10px;
      font-size: 12px;
      font-weight: 500;
      background: #f7f9fa;
      min-width: 75px;
      height: 27px;
      border: solid 1px #ebeff2;
      white-space: nowrap;

      & > span {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      .red-info svg {
        margin: 0 0 0 3px;
      }
    }

    td {
      font-size: 12px;
      height: 25px;
      vertical-align: middle;
      padding: 0 10px;
      border: solid 1px #ebeff2;
      white-space: nowrap;
    }

    svg {
      height: 14px;
      min-height: 14px;
      width: 14px;
      min-width: 14px;
      vertical-align: text-bottom;
      margin-right: 10px;
    }
  }
}
