[mode=import-prepare-data] {
  .folder-browser-title-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: none;

    &::before,
    &::after {
      content: none;
    }

    &.crosstab {
      .attribute-filter__warning-container {
        display: none;
      }

      .folder-browser-title {
        padding: 0 0 0 12px;
        display: flex;
        align-items: center;

        span:nth-child(2) {
          padding-left: 3px;
        }
      }
    }

    .folder-browser-title {
      max-width: calc(100vw - 340px);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      border: none;
      height: 50px;
      padding: 8px 20px 11px 12px;

      @media (max-width: 400px) {
        font-size: 0;
        text-indent: -1000em;
      }
    }

    .search-toolbar.prepare-data {
      height: auto;
      background-color: #ffffff;

      &:before, &:after {
        content: none;
      }

      #search-toolbar:not(.foo) {
        height: auto;

        .search-container {
          position: relative;
          float: none;
          top: 0;
          right: 0;

          .search-input {
            width: 200px;
            margin: 0 25px 0 10px;

            .ant-input {
              width: 200px;
              margin: 0 0 0 15px;
              background: #FFFFFF;
              border-radius: 20px;

            }

            .ant-input-suffix {
              left: 24px;
            }
          }
        }

        .ant-input-affix-wrapper {
          float: none;
          max-width: none;
          min-width: 0;
          width: auto;
        }
      }
    }
  }

  .filter-panel-container {
    border-top: 1px solid #E8EDF0;
    padding-bottom: 0;

    .switch-bar {
      text-align: right;
      padding: 0;
      height: 0;

      & > div {
        padding-top: 0 !important;
      }

      &:before, &:after {
        content: none;
      }

      .switch-container {
        position: absolute;
        right: 210px;
        bottom: 29px;

        .switch-label {
          padding: 0 6px;
          cursor: pointer;

          &::selection {
            background: inherit;
            color: inherit;
          }

          @media (max-width: 400px) {
              display: none;
          }
        }

        .ant-switch {
          min-width: 35px;
          background-color: #cecece;

          &-checked {
            background-color: #1890ff;
          }

          &:focus {
            outline: 3px solid #1890ff;
            outline-offset: 3px;
            box-shadow: none;
          }
        }
      }

      .subtotal-container {
        display: none;
      }
    }

    .search-toolbar-title,
    .selection-title-row {
      display: none;
    }

    .attributes-col {
    }
    
    .metrics-col {
      .mstr-icon.selector {
         top: 0;
      }
    }

    .filter-panel-selectors {
      padding: 0;
      margin-top: -20px;

      .checkbox-list,
      .filter-list {
        border: 1px solid rgba(151, 151, 151, 0.55);
        height: calc(100vh - 220px);
        max-height: 100vh;
        overflow: hidden;

        .ant-spin-spinning {
          height: calc(100vh - 222px);
          max-height: unset;
        }
      }

      .checkbox-list {
        .selector-loading-indicator {
          .ant-spin-container {
            height: calc(100vh - 222px);
          }
        }
        .checkbox {
          line-height: 22px;
          padding-left: 5px;
          padding-right: 0 !important;
          white-space: nowrap;
          cursor: pointer;

          &-empty {
            display: block;
            color: rgba(0, 0, 0, 0.4);
            width: 274px;
            padding-top: 16px;
            text-align: center;
          }

          input[type='checkbox'] {
            position: absolute;
            cursor: pointer;
            height: 0;
            width: 0;
            top: 4px;

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              height: 14px;
              width: 14px;
              border: 1px solid #d9d9d9;
              background: #fff;
              border-radius: 2px;
              cursor: pointer;
            }

            &:after {
              left: 4px;
              top: 2px;
              width: 6px;
              height: 9px;
              border: 1px solid white;
              border-width: 0 2px 2px 0;
              transform: rotate(45deg);
              content: "";
              position: absolute;
              display: none;
            }

            &:focus, &[tabfocused="1"] {
              outline: none !important;
            }

            &.focus-visible:before {
              outline: 3px solid #1890ff;
              outline-offset: 2px;
              box-shadow: none;
              border-color: #1890ff;
            }

            &:checked {
              &:before {
                background-color: #1890ff;
                border-color: #1890ff;
              }
              &:after {
                display: block;
              }
            }
          }

          .mstr-icon {
            padding-left: 17px;
          }

          &:hover {
            input[type='checkbox'] {
              &:before {
                border-color: #1890ff;
              }
            }
          }

          span:last-of-type {
            overflow: hidden;
            text-overflow: ellipsis;
          }

          span::selection {
            background: transparent;
            color: inherit;
          }
        }

        .ReactVirtualized__Grid__innerScrollContainer {
          max-width: none !important;
          display: table-row;
          margin-top: 3px;
        }

        .mstr-icon.selector svg {
          margin-left: 1px;
          margin-right: 3px;
        }

        .selector-list {
          height: calc(100vh - 249px) !important;
          margin-top: 5px;
        }

        .ant-empty-image {
          .mstr-icon {
            padding: 0;
          }
        }

        &.all-hidden {
          .selector-list {
            height: calc(100vh - 222px) !important;
            margin-top: 0px;
          }
        }
      }

      .filter-list {
        border-right: none;
        overflow-x: hidden;
        background: #FFFFFF;
        border-right: none;
        font-size: 13px;
        min-height: 175px;
        color: #444649;

        .filter-checkbox {
          display: inline-block;
          height: 24px;
          line-height: 24px;
          padding-left: 11px;
          vertical-align: middle;
          height: 30px;

          &:before {
            margin-left: -7px;
          }

          .ant-tree {
            margin-top: 0; 
          }
        }

        .ant-tree-title {
          display: inline-block;
          width: 100%;

          .filter-title-container {
            display: flex;

            .filter-title {
              overflow: hidden;
              text-overflow: ellipsis;
              flex-grow: 1;
              line-height: 21px;
            }

            .mstr-icon {
              line-height: 18px;
              top: 1px;
            }
          }
        }

        .ant-empty {
          top: calc(50% + 10px);
        }

        .ant-tree {
          height: calc(100vh - 222px);
          font-size: 13px;
          color: #444649;
          border: none;
          margin-top: 0;

          li {
            width: 100%;
            display: inline-block;
            height: 25px;
            padding: 0;
  
            .ant-tree-node-content-wrapper {
              padding: 0;
              line-height: 20px;
              transition: none;

              &:focus {
                background-color: #e6f7ff;
              }

              &-normal {
                width: 100%;
              }
            }
          }

          &-switcher.ant-tree-switcher-noop {
            display: none;
          }
        }

        .ant-list {
          overflow: auto;
          overflow-x: hidden;
        }
      }

      .selector-title {
        font-size: 10px;
        margin: 20px 0 5px 0;
        white-space: nowrap;
      }

      .ant-empty {
        position: absolute;
        top: calc(50% + 30px);
        left: 50%;
        margin: 0 -50% 0 0;
        transform: translate(-50%, -50%)
      }

      div:nth-child(1) {
        padding-left: 0 !important;
      }

      div:nth-child(3) {
        padding-right: 0 !important;
      }

      .filters-col {

      }
    }

    .ant-list,
    .empty-list {
      border: 1px solid rgba(151, 151, 151, 0.55);
      border-radius: 0;
      font-size: 13px;
      height: 175px;
      max-height: 175px;
      overflow: auto;
    }
  }

  .selected-element-icon {
      padding: 0 5px 0 0;
  }

  .ant-list.ant-list-selector,
  .ant-tree,
  .empty-list {
      background: #FFFFFF;
      border: 1px solid #DADADA;
      font-size: 13px;
      height: calc(100vh - 260px);
      min-height: 175px;
      overflow: auto;
  }

  .ant-list-bordered {
      border-radius: 0;
  }

  .ant-list-right {
      border-left: none;
  }

  .ant-list-bordered .ant-list-item {
      border: none;
      line-height: 30px;
      padding: 0 0 0 15px;
  }

  .ant-list-bordered .ant-list-item:nth-of-type(1) {
      padding-top: 4px;
  }

  .ant-list-item .ant-checkbox-wrapper {
      font-size: 12px;
      color: #444649;
  }

  .loading-indicator {
      height: 100%;
  }

  .ant-spin-nested-loading>div>.ant-spin {
      max-height: 448px;
  }

  .ant-checkbox-wrapper {
      white-space: nowrap;
  }

  .ant-checkbox-group {
      display: block;
  }
}

/* Data preview */
.ant-modal.modal-preview {
  left: 0;
  position: relative;

  .ant-modal-header {
    background-color: #FFFFFF;
    padding: 12px;

    .ant-modal-title {
      font-size: 14px;
      font-weight: 400;
    }
  }

  .ant-modal-body {
    padding: 24px 0 15px 0;

    & > div > div {
      padding: 0 24px;
    }
  }

  .ant-table-wrapper {
    .ant-spin-nested-loading {
      .ant-spin-container {
        width: fit-content;
      }

      width: fit-content;
      overflow: visible;
      min-height: 173px;
      height: 100% !important;
      width: 100%;
      position: relative;

      div {
        .ant-spin {
          max-height: 430px;
          position: relative;

          .loading-text-container {
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }

      .ant-spin-container {

        .ant-table#mstr-table {
          border: none;
          width: fit-content;

          .ant-table-content {
            border: none;

            .ant-table-scroll {
              .ant-table-body {
                .ant-table-fixed {
                  padding: 0;
                  border: 1px solid #e8e8e8;

                  .ant-table-thead {
                    tr {
                      th {
                        font-size: 15px;
                        background-color: #FFFFFF;
                        overflow: hidden;
                        white-space: nowrap;
                        font-size: 12px;
                        font-weight: bold;
                        padding: 5px 18px;

                        &.metric-column {
                          text-align: center !important;
                        }

                        &.attribute-column {
                          text-align: center !important;
                        }
                      }
                    }
                  }

                  .ant-table-tbody {
                    tr {
                      td {
                        font-size: 14px;
                        font-size: 12px;
                        font-weight: normal;
                        padding: 5px 18px;

                        &.metric-column,
                        &.attribute-column {
                          white-space: nowrap;
                          overflow: hidden;
                          text-overflow: ellipsis;
                          text-align: center;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .preview-note {
    font-size: 10px;
    color: #9A9A9A;
    letter-spacing: 0.06px;
    line-height: 26px;
    padding-top: 0;

    span:nth-of-type(2) {
      vertical-align: sub;
    }
  }

  #close-preview-section {
    text-align: right;
    border-top: 1px solid #E8EDF0;
  }

  #close-preview-section .ant-radio-button-wrapper {
      margin-top: 15px;
  }

  .preview-info-icon {
      vertical-align: middle;
  }
}

.selected-element {
  display: inline-block;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 180px;
  line-height: 24px;
  height: 24px;
}

.selected-element-icon {
  padding: 0 5px 0 0;
}

.ant-list-right {
  border-left: none;
}

.ant-list-bordered .ant-list-item {
  border: none;
  padding-top: 2px;
  padding-bottom: 2px;
}

.loading-indicator {
  height: 100%;
}

.loading-indicator-tree {
  height: 430px;
}

.ant-spin-text {
color: rgba(0, 0, 0, 0.5);
  font-size: 13px;
}

.ant-checkbox-wrapper {
  white-space: nowrap;
}

.ant-checkbox-group {
  display: block;
}

/* Tree */
.ant-tree {
  font-size: 12px;

  li {
    padding-top: 0;
    padding-bottom: 0;
  }

  &-iconEle {
    font-weight: bold;
    vertical-align: middle;
  }

  &.ant-tree-dirctory {
    & > li span.ant-tree-node-content-wrapper {
      &:hover {
        background: rgba(24, 144, 255, 0.1);
      }

      &:focus {
        outline: none;
      }
    }

    .ant-tree-child-tree {
      & > li span.ant-tree-node-content-wrapper {
        &.ant-tree-node-selected:focus {
          color: rgba(0, 0, 0, 0.65);
        }

        &:hover, &:hover {
          background: rgba(24, 144, 255, 0.1);
        }

        &.ant-tree-node-selected, .ant-tree.ant-tree-directory>li span.ant-tree-node-content-wrapper.ant-tree-node-selected {
          background: #1890ff !important;
        }

        &:not(.ant-tree-node-selected):focus:before {
          background: #e6f7ff;
        }
      }
    }
  }
}

.rstudio-connector-params label {
  font-size: 13px;
  color: rgba(0, 0, 0, 0.65);
}
